import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuerySales, VhEcommerce } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-by-product-tiktok',
  templateUrl: './by-product-tiktok.component.html',
  styleUrls: ['./by-product-tiktok.component.scss']
})
export class ByProductTiktokComponent implements OnInit {

  listProduct: Array<any> = []; //danh sách product theo từng id_shop
  listSubProduct: Array<any> = []; //danh sách product theo từng id_shop
  /**----------------------------------- */
  infoShop: any = {};
  searchValue: any = '';
  heightScroll: any;
  setHeight = { "max-height": "500px", "height": "500px" };
  sortQuantity: 'asc' | 'desc' = 'asc';
  indexSegment: number = 0;
  id_branch: string = this.vhQuerySales.getDefaultBranch()._id;
  constructor(
    private router: Router,
    public lang: LanguageService,
    public vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private cdRef: ChangeDetectorRef,
    private vhEcommerce: VhEcommerce
  ) {
  }

  ngOnInit(): void {
    this.getData();
  }
  getData() {
    this.infoShop = this.vhEcommerce.getlocalShopListTiktok()[0];
    this.changeShop(this.infoShop._id);
  }
  /**Chọn shop và lọc sản phẩm theo shop đó */
  changeShop(id) {
    this.listProduct = [];
    this.syncProductTiktok();
    this.listSubProduct = [...this.listProduct];
  }
  /**Đem các biến thể ra đứng độc lập, mục đích dễ sort */
  syncProductTiktok() {
    this.vhEcommerce.getlocalProductListTiktok(this.infoShop['_id']).forEach(item => {
      if (item.status == 'ACTIVATE') {
        if (item.skus && item.skus.length) {
          for (let sku of item.skus) {
            let data = {
              name: item.title, create_time: new Date(+item.create_time),
              id: sku.id, update_time: new Date(+item.update_time), ...sku, id_product: item.id
            };
            this.listProduct.push(data);
          }
        }
      }
    });
    this.listSubProduct = [...this.listProduct];
  }
  // renderStockInfo(stock_info: Array<any>) {
  //   let normal_stock: number = 0
  //   let data = stock_info.find(item => item.stock_type == 2)
  //   data && (normal_stock = data.normal_stock)
  //   return normal_stock
  // }
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/update-stock']);
  }
  searchTiktok(value, products?) {
    let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase());
    this.listProduct = this.vhAlgorithm.searchList(val, products ? [...products] : [...this.listSubProduct], ['name']);
  }
  ngAfterViewChecked() {
    if (document.querySelector("#by-product-tiktok-stock") && document.querySelector(".ant-table-thead") && document.querySelector(".by-product-tiktok-stock-header")) {
      this.heightScroll = document.querySelector("#by-product-tiktok-stock").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".by-product-tiktok-stock-header").clientHeight - 90 + "px";
    }
    this.cdRef.detectChanges();
  }
  /**
   * Sort số lượng tồn kho
   */
  sortQuantityFunc() {
    this.searchTiktok(this.searchValue);
    if (this.sortQuantity == 'asc') {
      this.listProduct = this.vhAlgorithm.sortNumberbyASC([...this.listProduct], 'normal_stock');
      this.sortQuantity = 'desc';
    } else {
      this.listProduct = this.vhAlgorithm.sortNumberbyDESC([...this.listProduct], 'normal_stock');
      this.sortQuantity = 'asc';
    }
  }
  trackByFn(index, item) {
    return index;
  }
  /**Kiểm tra input nhập vào của stock */
  checkStock(product, event) {
    if (typeof event == 'string')
      delete product.newQuantity;
    else product.newQuantity = event;
  }
  /**Cập nhật stock */
  updateQuantity(product) {
    product.loading = true
    this.vhEcommerce.updateStockTiktok(
      this.infoShop['shop_cipher'],
      this.infoShop['app_key'],
      this.infoShop['access_token'],
      product.id_product.toString(),
      this.renderFormUpdate(product),
      this.infoShop['_id']
    ).then((res: any) => {
      product.inventory[0].quantity = product.newQuantity;
      product.newQuantity = null;
      this.vhComponent.alertMessageDesktop("success", this.lang.translate("Update successfully"));
    }).finally(() => {
      if (product) product.loading = false;
      else product.loading = false;
    });
  }

  /**Kiểm tra xem sau khi update thì quantity có bằng với quantity khi update hay không */
  checkQuantityAfterUpdate(product, productShopee) {
    // if (product.newQuantity == productShopee.stock_info_v2.summary_info.total_available_stock)
    //   return true
    // return false
    // if (product.model) {
    //   let modelShopee = productShopee.model.find(item => item.model_id == product.model.model_id)
    //   if (modelShopee) {
    //     let stock_info_v2 = modelShopee.stock_info_v2.summary_info.total_available_stock
    //     if (stock_info_v2 == product.newQuantity) {
    //       product.normal_stock = stock_info_v2
    //       delete product.newQuantity
    //       return true
    //     } return false
    //   }
    // } else {
    //   let stock_info = productShopee.stock_info_v2.summary_info.total_available_stock
    //   if (stock_info && stock_info == product.newQuantity) {
    //     product.normal_stock = stock_info
    //     delete product.newQuantity
    //     return true
    //   } else return false
    // }

  }
  /**chuyển data theo đúng định dạng update */
  renderFormUpdate(sku) {
    return {
      "skus": [
        {
          "id": sku.id,
          "inventory": [
            {
              "warehouse_id": sku.inventory[0].warehouse_id,
              "quantity": sku.newQuantity
            }
          ]
        }
      ]
    };
  }

}
